@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@600&display=swap);
body {
  font-family: 'Barlow', sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  --window-height: calc(100vh - 2rem);
}

code {
  white-space: pre;
}
.room{position:absolute;top:0;left:0;right:0;bottom:0;-webkit-user-select:none;-ms-user-select:none;user-select:none;background:#ccc;display:grid;grid-template-rows:-webkit-min-content 2fr;grid-template-rows:min-content 2fr;grid-auto-rows:1fr;border-radius:4px;overflow:hidden;grid-template-areas:"controls" "status"}.picker .room{position:static}.room.free{background:#93E0FF}.room.busy{background:#C80037}.room .controls{background:#fff;grid-area:controls;display:grid;grid-template-columns:1fr 1rem;grid-template-rows:1rem;padding:.5rem;line-height:1rem;border-bottom:1px solid rgba(0,0,0,0.25)}.room .controls .button{display:none}.room.active{box-shadow:0 0 0 0.125rem yellow}.room.active .controls .button{display:block}.room.open .controls{grid-template-columns:1.5rem 1fr 1rem}.room.open .controls .button{display:block}.room.open .controls .button.back{margin-right:.5rem}.room .status{display:grid;place-items:center;grid-area:status;font-size:3rem;color:#fff;font-weight:600}.room .aspects{fill:#fff;display:grid;grid-auto-flow:column;grid-auto-columns:1fr;padding:1rem;place-items:center}.room .aspects svg{max-width:8rem;max-height:8rem}

.picker{display:grid;grid-template-columns:repeat(auto-fit, minmax(13rem, 1fr));grid-gap:1rem;gap:1rem;padding:1rem;background:black;box-sizing:border-box;min-height:100vh}

.MonitorPanel{height:var(--window-height);display:grid;grid-template-rows:1fr;margin:1rem;position:relative}svg{width:100%;height:100%}

